}



.Header{
  flex:1 1;
  -webkit-text-fill-color:azure;
  color:azure;
  overflow:hidden;
   background-image: linear-gradient(to bottom ,rgba(0,0,0,.2),rgba(0,0,0,.5),rgba(0,0,0,.8),rgba(0,0,0,1)),url(https://i.pinimg.com/originals/44/6e/3b/446e3b79395a287ca32f7977dd83b290.jpg);
